.index-page {
  position: relative;
  min-width: 320px;
  font-family: @font-family-secondary;
  font-size: @font-size-primary;
  line-height: @line-height-primary;
  font-weight: 400;
  font-style: normal;
  color: rgb(64 64 64);
  background-color: white;
}

.is-locked {
  overflow: hidden;
}

.dark {
  color: white;
  background-color: #0f0f0f;
}

.dark__button {
  color: white;
}

.index-main {
  padding-top: 80px;
}
