@font-face {
  font-weight: 500;
  font-family: "Arial";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/arial.woff2") format("woff2"), url("../fonts/arial.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  outline: none;
}
html {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
@mixin list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@mixin reset {
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: fixed;
  padding: 20px 30px 15px;
  width: 100%;
  background-color: inherit;
  z-index: 5;
  /*  &--mod {
    padding: 15px 30px 15px;
    background-color: rgba(255, 255, 255, 1);
    transition-duration: 0.8s;
  }*/
}
.header__nav {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 30px;
  align-items: baseline;
  z-index: 1;
}
.header__logo {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  font-family: "Lusitana", "Arial", sans-serif;
  letter-spacing: 0.04em;
  z-index: 2;
}
.header__button {
  position: relative;
  display: flex;
  justify-content: flex-end;
  letter-spacing: 0.04em;
  color: black;
  z-index: 2;
}
.header__button-span {
  font-family: "Lusitana", "Arial", sans-serif;
  display: flex;
  justify-content: flex-end;
  font-size: 20px;
  line-height: 20px;
  transition-duration: 0.6s;
}
.header__button-span--nav {
  transform: translateX(50%);
  opacity: 1;
}
.header__button-span--close {
  transform: translateX(-200%);
  opacity: 0;
}
.header__button-span--nav-mod {
  transform: translateX(0);
  opacity: 1;
}
.header__button-span--close-mod {
  transform: translateX(-50%);
  opacity: 0;
}
.header__button-theme {
  grid-column-start: 2;
  text-align: end;
  align-items: flex-start;
  font-size: 20px;
  line-height: 20px;
  font-family: "Lusitana", "Arial", sans-serif;
  color: inherit;
  margin-bottom: 10%;
  z-index: 2;
}
.header__wrap-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  position: fixed;
  opacity: 0;
  left: 0;
  top: 0;
  height: 100vh;
  min-height: 640px;
  width: 100%;
  padding: 30px;
  z-index: 1;
  animation-name: lift-down;
  animation-duration: 0.6s;
  transition-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.7, 0.8, 0, 1);
}
.header__wrap-list--open {
  opacity: 1;
  animation-name: lift-up;
  animation-timing-function: cubic-bezier(0.7, 0.8, 0, 1);
}
.header__list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
}
.header__list:last-child {
  display: flex;
  justify-content: flex-end;
  flex-flow: row wrap;
  width: 100%;
  margin-top: 10%;
  align-items: center;
}
.header__list:last-child .header__item {
  margin-left: 20px;
}
.header__list:last-child .header__item:first-child {
  margin-left: 0;
}
.header__list:last-child .header__link {
  font-size: 18px;
  line-height: 18px;
  font-family: "Lusitana", "Arial", sans-serif;
  color: inherit;
}
@keyframes lift-up {
  from {
    transform: translateX(120%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes lift-down {
  from {
    transform: translateX(0);
  }
  100% {
    transform: translateX(120%);
  }
}
.header__item {
  padding: 10px 0;
}
.header__link {
  font-size: 35px;
  line-height: 45px;
  font-family: "Lusitana", "Arial", sans-serif;
}
.header__link-button {
  font-size: 18px;
  line-height: 18px;
  font-family: "Lusitana", "Arial", sans-serif;
  color: inherit;
}
.intro,
.skills {
  padding: 0 30px 30px;
}
.intro__title {
  position: relative;
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 50px;
  line-height: 65px;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: center;
  border-bottom: 1.5px solid black;
  padding: 30px 0;
  letter-spacing: 0.04em;
}
.intro__desc {
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.04em;
  color: rgb(134 134 139);
}
.intro__photo {
  width: 100%;
  margin-bottom: 20px;
}
.skills__title {
  font-size: 30px;
  font-family: "Lusitana", "Arial", sans-serif;
  line-height: 30px;
  font-weight: 700;
  letter-spacing: 0.04em;
  margin-bottom: 15px;
}
.skills__item {
  padding: 5px 0;
}
.skills__desc {
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.04em;
  color: rgb(134 134 139);
}
.index-page {
  position: relative;
  min-width: 320px;
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: rgb(64 64 64);
  background-color: white;
}
.is-locked {
  overflow: hidden;
}
.dark {
  color: white;
  background-color: #0f0f0f;
}
.dark__button {
  color: white;
}
.index-main {
  padding-top: 80px;
}
